
import '../App.css';
import headshot from "../statics/headshot.JPG"
import Typist from "react-typist";
import {useState, useEffect} from "react";



function HelloWorld() {
    const [count, setCount] = useState<number>(1);
    useEffect(() => {console.log(count);setCount(1000);});
    return (<div>
        <div>
        <img src={headshot} className="headshot"/>
        </div>
        <h1>
        Hello World!
        </h1>
        <div>
            {count ? (
                <Typist className="typingAnimation" onTypingDone = {() => setCount(0)}>
                My name is Siddharth.
                <Typist.Delay ms={500}></Typist.Delay>
                <Typist.Backspace count = {"My name is Siddharth. Welcome.".length}/>
                <Typist.Delay ms={500}></Typist.Delay>
                Python expert.
                <Typist.Delay ms={500}></Typist.Delay>
                <Typist.Backspace count = {"Python expert.".length}/>
                Java enthusiast.
                <Typist.Delay ms={500}></Typist.Delay>
                <Typist.Backspace count = {"Java enthusiast.".length}/>
                Explorer.
                <Typist.Delay ms={500}></Typist.Delay>
                <Typist.Backspace count = {"Explorer.".length}/>
                <Typist.Delay ms={500}></Typist.Delay>
                </Typist>
            ) : ("")}
            </div>
        </div>)
}
export default HelloWorld